.modal {
  height: 100vh;
  width: 100vw;
  background-color: #A9A9A9;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  pointer-events: none;
  transition: 0.5s;

}

.modal.active {
  opacity: 1;
  pointer-events: all;
}

.modal__content {
  padding: 20px;
  border-radius: 12px;
  transform: scale(0.5);
  transition: 0.4s all;
  width: 50vw;
  background-color: #DCDCDC;
  font-size: 22px;
  color: #696969;
}

.modal__content.active {
  transform: scale(1);
}

.input, .select_type {
  &__control {
    width: 150px;
    margin-left: 25%;
    background-color: transparent !important;
    border: 1px solid #008080 ! important;
    border-radius: 5px !important;
    border-bottom: 1px solid #008080 ! important;
    box-shadow: none !important;
  }

  &--menu-is-open {
    border-color: var(--primary-color) !important;
  }

  &__value-container {
    padding: 0 !important;
    cursor: text !important;
    color: #696969 !important;
  }

  &__input-container {
    color: #696969 ! important;
  }

  &__indicator-separator {
    display: none !important;
  }

  &__indicators {
    cursor: pointer !important;
  }

  &__indicator {
    color: #696969 !important;

    &:hover {
      color: #008080 ! important;
    }
  }

  &__menu {
    background-color: #DCDCDC !important;
    color: rgba(#696969, 0.6) ! important;
  }

  &__single-value {
    color: #696969 !important;
  }

  &__option {
    cursor: pointer !important;

    &--is-focused {
      background-color: lighten(#008080, 8%) ! important;
    }
  }

  display: flex;
  width: 250px;
  height: 50px;
  margin-left: 15%;
  margin-top: 20px;
  margin-bottom: 10px;
  justify-content: center;
  align-items: center;
  background: #DCDCDC;
  color: #696969;
  border-radius: 5px;
  border-color: #008080;
  display: flex;
  font-size: 18px;
}

p{
  margin-left: 25px;
}
.tableAdd {
  width: 100%;
  align-items: center;
  text-align: center;
  border-collapse: collapse;
  table-layout: fixed; 
}
table.tableAdd th,
table.tableAdd td {
  border: 0px solid;
}

.but{
  display: flex;
  width: 150px;
  height: 40px;
  margin-left: 22%;
  margin-top: 10px;
  justify-content: center;
  align-items: center;
  background: #DCDCDC;
  color: #696969;
  border-radius: 5px;
  border-color: #008080;
  font-size: 22px;
  flex-shrink: 0;
  cursor: pointer;
}



