input{
  display: flex;
  width: 130px;
  height: 25px;
  margin-left: 30%;
  justify-content: center;
  align-items: center;
  background: #DCDCDC;
  color: #696969;
  border-radius: 5px;
  border-color: #008080;
  font-size: calc(7px + 2vmin);
  flex-shrink: 0;
}

input::placeholder {
  font-size: 15px;
}

.body {
  background-color: #DCDCDC;
  min-height: 100vh;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: #696969;
  text-align: center;
  align-items: center;
}

.bod {
  background-color: #DCDCDC;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: #696969;
  text-align: center;
  align-items: center;
}

.App-link {
  color: #008080;
}

.flower, .flower.th, .flower.td{
  border-collapse: collapse;
  border: 3px solid #008080;
  width: 98%;
  margin: 0 auto;
  max-width: 100%;
}
.resources{
  border-collapse: collapse;
  border: 1px solid #008080;
  width: 100%;
}

.none{
  border-collapse: collapse;
  border: none;
  width: 100%;
}


table.none th{
  border: 0px solid;
}

.flower th, .flower td {
  border: 3px solid #008080;
  text-align: center;
  padding: 10px;
  
}

.selection{
  width: 98%;
  margin: 0 auto;
  max-width: 100%;
  text-align: center;
  align-items: center;
}

.selection th, .selection tr, .selection th{
  border: 0;
  border-collapse: collapse; 
  text-align: center;
  align-items: center;
  margin: 0 auto;
}

.add{
  display: flex;
  width: 250px;
  height: 55px;
  margin-left: 16%;
  margin-top: 10px;
  justify-content: center;
  align-items: center;
  background: #DCDCDC;
  color: #696969;
  border-radius: 5px;
  border-color: #008080;
  font-size: 22px;
  flex-shrink: 0;
  cursor: pointer;
}

.waterShed{
  display: flex;
  height: 30px;
  margin-left: 80px;
  justify-content: center;
  align-items: center;
  background: #DCDCDC;
  color: white;
  border-radius: 5px;
  border-color: #008080;
  font-size: calc(4px + 2vmin);
  flex-shrink: 0;
}
.butRes{
  display: flex;
  height: 60px;
  margin-left: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
  justify-content: center;
  align-items: center;
  background: #282c34;
  color: white;
  border-radius: 5px;
  border-color: #008080;
  font-size: calc(7px + 2vmin);
  flex-shrink: 0;
}
.navigates{
  display: flex;
  height: 50px;
  width: 150px;
  margin-left: 20px;
  justify-content: center;
  align-items: center;
  background: #008080;
  color: white;
  border-radius: 5px;
  border-color: #008080;
  font-size: calc(8px + 2vmin);
  flex-shrink: 0;
}
.inp{
  height: 55px;
  margin-left: 0px;
}

.in{
  margin-left: 87%;
}
.container {
  display: flex;
  justify-content: space-between; /* Распределяет пространство между элементами */
  flex-direction: column; /* Устанавливаем направление столбца */
}
.cont{
  display: flex;
  justify-content: space-between; /* Распределяет пространство между элементами */
  flex-direction: row; /* Устанавливаем направление столбца */
}